import React from "react";
import PropTypes from 'prop-types'
import { Link } from "react-router-dom";
import { Drawer, List, ListItem, ListItemText, Box, Grid, Avatar, Tooltip} from "@material-ui/core"; 
import { green } from '@material-ui/core/colors';
import { red } from '@material-ui/core/colors';
import { blue } from '@material-ui/core/colors';
import { yellow } from '@material-ui/core/colors';

import WbSunnyIcon from '@material-ui/icons/WbSunny';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';

import ListItemIcon from '@material-ui/core/ListItemIcon';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import AssignmentIcon from '@material-ui/icons/Assignment';
import HomeIcon from '@material-ui/icons/Home';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

const MyDrawer = props => {
   const { variant, open, onClose, onItemClick, title, handleClickOpen, handleClose, removeAllLocation } = props
   
  return (
      <Drawer
        variant={variant}
        open={open}
        onClose={onClose}
      >
	  <Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt='COVID'  >
			  <VpnKeyOutlinedIcon color="primary" />
		  </Avatar>
		  <Avatar alt='COVID'  >
			  {title}
		  </Avatar>
		  <Avatar >{title.slice(0, 1)}</Avatar>
		</Grid> 
		<Box mt={2} />
        <div/>
        <List>
          <ListItem
            button
            component={Link}
            to="/"
            onClick={onItemClick("Show Weather")}
          >
		  <ListItemIcon>
			<WbSunnyIcon style={{ color: blue[500] }}/>
		  </ListItemIcon>
            <ListItemText>Show Weather</ListItemText>
          </ListItem>
          <ListItem
            button
            onClick={(event) => handleClickOpen()}
          >
		  <ListItemIcon>
				<AddIcon color="primary"/>
		  </ListItemIcon>
            <ListItemText>Add City</ListItemText>
          </ListItem>
          <ListItem
            button
            onClick={(event) => removeAllLocation()}
          >
		  <ListItemIcon>
				<RemoveIcon color="secondary"/>
		  </ListItemIcon>
            <ListItemText>Remove All City</ListItemText>
          </ListItem>        
		  </List>
      </Drawer>

  )
  }

MyDrawer.propTypes = {
	variant: PropTypes.string.isRequired, 
	open: PropTypes.bool.isRequired, 
	onClose: PropTypes.func.isRequired, 
	onItemClick: PropTypes.func.isRequired, 
	title: PropTypes.string.isRequired, 
	handleClickOpen: PropTypes.func.isRequired, 
	handleClose: PropTypes.func.isRequired,  			
	removeAllLocation	: PropTypes.func.isRequired
}
  
export default MyDrawer
